:root {
    --default-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Ubuntu, "Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "Microsoft Yahei UI", "Microsoft Yahei", "Source Han Sans CN", sans-serif;
  }
  
  * {
    box-sizing: border-box;
  }
  
  body {
    font-size: 14px;
    font-family: Poppins, var(--default-font-family);
  }
  
  .main-container {
    width: 100%;
    max-width: 1440px;
    height: 100vh;
    margin: 0 auto;
    background: #ffffff;
    overflow: hidden;
    text-align: center;
    padding: 20px;
  }
  
  .care-logo {
    display: block;
    margin: 50px auto;
    width: 100px; /* Adjust the width as per your preference */
    height: 150px;
  }
  
  .returning-user {
    font-family: Poppins, var(--default-font-family);
    font-size: 18px;
    font-weight: 700;
    margin-top: 20px;
  }
  
  .rectangle-button,
  .rectangle-button-2 {
    display: block;
    width: 90%;
    max-width: 343px;
    height: 60px;
    margin: 10px auto;
    background: #004092;
    border: none;
    border-radius: 30px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }
  
  .admin-span,
  .user-span {
    color: #ffffff;
    font-family: Poppins, var(--default-font-family);
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    line-height: 60px;
  }
  
  .new-here-div {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-family: Poppins, var(--default-font-family);
    font-size: 16px;
  }
  
  .text-5 {
    color: #000000;
    margin-right: 10px;
  }
  
  .text-6 {
    color: #004092;
    cursor: pointer;
  }
  
  /* Responsive Styles */
  @media (max-width: 1024px) {
    .care-logo {
      width: 120px;
    }
    .rectangle-button,
    .rectangle-button-2 {
      width: 80%;
    }
  }
  
  @media (max-width: 768px) {
    .care-logo {
      width: 100px;
    }
    .rectangle-button,
    .rectangle-button-2 {
      width: 70%;
    }
  }
  
  @media (max-width: 480px) {
    .rectangle-button,
    .rectangle-button-2 {
      width: 90%;
    }
    .care-logo {
      width: 80px;
    }
  }
  